.profile__title {
  margin: 0;
  padding-right: 17px;
  font-weight: 500;
  font-size: 42px;
  line-height: 56px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media (max-width: 660px) {
  .profile__title {
    font-size: 27px;
    padding-right: 10px;
  }
}
