.dialog {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  display: flex;
  background: rgb(0 0 0 / 0.5);
  flex-direction: column;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.6s, opacity 0.5s linear;
}
